.navs {
  height: 60px;
  border-bottom: 1px solid rgba(0,0,0,.2);
  border-top: 1px solid rgba(0,0,0,.2);
  background-color: rgba(0,0,0,.8);
  position: sticky;
  top: 0;
  z-index: 3;
}


.navs ul {
  display: flex;
}

.navs ul li {
  list-style: none;
  line-height: initial;
}

#a{
  padding: 20px 30px;
 line-height: 60px;
 color: #fff;
 text-decoration: none;
 text-transform: uppercase;
 font-weight: bold;
}

.navs .service a {
  color: black;
 text-decoration: none;
 font-size: 13px;
 text-transform: none;
 transition: 0.5s;
}

.navs .service li {
  margin: 10px;
}
.link:hover .service{
  display: block;
}
.navs .service {
  display: none;
}
.navs .service ul{
  display: flex;
  flex-direction: column;
}

.navs .service li .underline {
  height: 3px;
  width: 0;
  transition: width 0.5s ease;
  background-color: lightblue;
}
.navs .service li:hover .underline {
  width: 100px;
  transition: width 0.5s ease;
}

.navs .service li:hover a {
  opacity: 0.4;
  transition: 0.5s;
}